.loading {
  width: 100%;
  height: 100%;
  z-index: 9999;
  background: #000;
  position: fixed;
  top: 0;
  left: 0;
}

.loading.is-hidden {
  display: none;
}

.loading-text {
  text-align: center;
  width: 100%;
  height: 100px;
  margin: auto;
  line-height: 100px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.loading-text span {
  color: #fff;
  margin: 0 5px;
  font-family: Quattrocento Sans, sans-serif;
  display: inline-block;
}

.loading-text span:nth-child(1) {
  filter: blur();
  animation: blur-text 1.5s linear infinite alternate;
}

.loading-text span:nth-child(2) {
  filter: blur();
  animation: blur-text 1.5s linear .2s infinite alternate;
}

.loading-text span:nth-child(3) {
  filter: blur();
  animation: blur-text 1.5s linear .4s infinite alternate;
}

.loading-text span:nth-child(4) {
  filter: blur();
  animation: blur-text 1.5s linear .6s infinite alternate;
}

.loading-text span:nth-child(5) {
  filter: blur();
  animation: blur-text 1.5s linear .8s infinite alternate;
}

.loading-text span:nth-child(6) {
  filter: blur();
  animation: blur-text 1.5s linear 1s infinite alternate;
}

.loading-text span:nth-child(7) {
  filter: blur();
  animation: blur-text 1.5s linear 1.2s infinite alternate;
}

@keyframes blur-text {
  0% {
    filter: blur();
  }

  100% {
    filter: blur(4px);
  }
}

